body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  background-color: #060606;
  background-color: var(--black);
}

a {
  text-decoration: none;
}

.App {
  position: relative;
  height: 100vh;
  background-image: linear-gradient(#060606cc, #060606ff), url(/D2-Clan-Titles/static/media/Traveler-min.72f644f8.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  overflow: hidden;
}

:root {
  --black: #060606;
  --onyx: #353B3C;
  --white: #F7F0F5;
  --red: #E86252;
  --bungieBG: #232936;
  --bungieWhite: #f5f5f5;
}

div::-webkit-scrollbar {
  width: 5px;
}
div::-webkit-scrollbar-track {
  background: #f1f1f100; 
}
div::-webkit-scrollbar-thumb {
  border-radius: 5px;
  background: #88888888; 
}
div::-webkit-scrollbar-thumb:hover {
  background: #555; 
}

.loading,
.loading:before,
.loading:after {
  background: #ffffffaa;
  -webkit-animation: load1 1s infinite ease-in-out;
  animation: load1 1s infinite ease-in-out;
  width: 1em;
  height: 4em;
}
.loading {
  color: #ffffffaa;
  text-indent: -9999em;
  margin: 0px auto;
  position: relative;
  top: 45%;
  font-size: 11px;
  -webkit-transform: translateZ(0);
  transform: translateZ(0);
  -webkit-animation-delay: -0.16s;
  animation-delay: -0.16s;
}
.loading:before,
.loading:after {
  position: absolute;
  top: 0;
  content: '';
}
.loading:before {
  left: -1.5em;
  -webkit-animation-delay: -0.32s;
  animation-delay: -0.32s;
}
.loading:after {
  left: 1.5em;
}
@-webkit-keyframes load1 {
  0%,
  80%,
  100% {
    box-shadow: 0 0;
    height: 4em;
  }
  40% {
    box-shadow: 0 -2em;
    height: 5em;
  }
}
@keyframes load1 {
  0%,
  80%,
  100% {
    box-shadow: 0 0;
    height: 4em;
  }
  40% {
    box-shadow: 0 -2em;
    height: 5em;
  }
}

